import quotes from './quotes-info';

const quoteText = window.document.querySelector('.quote__text');
const quoteAuthor = window.document.querySelector('.quote__author');

const displayQuote = (quote) => {
  quoteText.innerText = quote.text;
  quoteAuthor.innerHTML = `&ndash; ${quote.author}`;
};

const getRandomQuote = () => {
  const quotesLength = quotes.length;
  const randomIndex = Math.floor(Math.random() * quotesLength);
  const quote = quotes[randomIndex];

  return quote;
};

export const generateDisplayRandomQuote = () => displayQuote(getRandomQuote());

export const quotesInit = () => {
  generateDisplayRandomQuote();

  // Chaching needed Dom Elements
  const quoteBtn = window.document.querySelector('.quote__btn');

  // Add click event to generator button to get random quote and display it.
  quoteBtn.addEventListener('click', generateDisplayRandomQuote);
};
